<template>
    <div class="member-tile">
        <a v-if="hasLink" :href="data.linkedin" class="member-text link">
            <p class="member-text name">{{ data.name }}</p>
        </a>
        <p v-else class="member-text name">{{ data.name }}</p>
    </div>
</template>

<script>
export default {
    name: "MemberTile",
    props: {
        data: {
            type: Object,
            required: true
        }
    },
    computed: {
        hasLink() {
            return this.data.linkedin !== "";
        }
    }
}
</script>

<style lang="scss">
.member-tile {
  width: fit-content;
  
  .member-text {
    text-decoration: none; /* Remove underline from text */
    color: var(--medium);
    padding: 0;
    margin: 0;

    &.link { /* Apply specific styles for the link class */
      &:hover {
          cursor: pointer;
        text-decoration: underline; /* Add underline on hover */
      }
    }
  }
}
</style>